import {uniPopup} from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue" 
export default {
	data() {  
		return {
		details:[],
		id:""
		}
	},
	onLoad:function(option){ 
		var _self = this
		_self.id = option.id
		console.log(_self.id)
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:_self.id,pull:8,optionId:uni.getStorageSync("openId"),branch:0},
				method:"GET",
				success:function(res){
					console.log(res)
					_self.details = res.data[0]
					console.log(res)
				}
			})
		},
	},
	 components: {uniPopup},
	
}
